import { AnyAction, applyMiddleware, combineReducers, createStore } from "redux";
import { tasksReducer } from "./tasks-reducers/tasks-reducer";
import { todolistsReducer } from "./todoList-reducers/todolists-reducer";
import thunk, { ThunkDispatch } from "redux-thunk"

// Provider  App
// reducer    
//tasksReducer -   
//todolistsReducer -  todolist


export type AppRootState = ReturnType<typeof rootReducer>

const rootReducer = combineReducers({ // dispatch   rootReducer,      
  //  
  todolist: todolistsReducer,
  tasks: tasksReducer
})

export const store = createStore(rootReducer, applyMiddleware(thunk)) // thunk
//  :    -,   
export type AppDispatchType = ThunkDispatch<AppRootState, unknown, AnyAction> //   action

//@ts-ignore
window.store = store

